@import '../variables';

.CategoryWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin: 30px 0px;
  li {
    font-family: $noto;
    margin: 13px 0px;
    font-size: 17px;
    cursor: pointer;
    font-weight: 400;
    &.clicked {
      font-weight: 800;
      font-size: 19px;
    }
  }
}
