@import '../variables';
.UserWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin: 20px 0px;
  .BlackBtn,
  .WhiteBtn {
    width: 70px;
  }
  .NavId {
    margin: 10px 0 10px 0;
    font-family: $gowun;
    font-size: 20px;
  }
  .NavIcon {
    width: 100px;
    height: 100px;
    border-radius: 70%;
  }
}
